<template>
    <!-- <div>
            <input type="email" name="email" placeholder="Enter Email" v-model="email">
            <input type="password" name="password" placeholder="Password" v-model="password">
            <button @click="register">Register</button>
            <button @click="signInwithGoogle">Register with Google</button>
    </div> -->
    <div class="page">
        <div class="content">
            <p class="title">
                <font-awesome-icon :icon="['fas', 'square-pen']" />  
                <span class="adi"> A</span>dah's <span class="adi">Di</span>ary
            </p>
            <h1 class="sub-title">CREATE YOUR ACCOUNT</h1>
            <form @submit="register">
                <div class="form-fields">
                    <input type="email" class="input" name="email" placeholder="Enter Email" v-model="email" required>
                </div>
                <div class="form-fields">
                    <input type="password" class="input"  name="password" placeholder="Password" v-model="password" required>
                </div>
                <div class="error-message">
                    <p class="error-msg" v-if="errorStat">{{ errorMsg }}</p>
                </div>
                <div class="footer">
                        <button  :class="buttonStat? 'disabled-btn':'button'" type="submit" :disabled="buttonStat">{{ buttonName }}</button>
                        <p @click="goBackToLogIn" class="go-back">Go back to Log In page</p>
                        <p>or</p>
                </div>
            </form>
            <div class="google" @click="signInwithGoogle ">
                    REGISTER WITH GOOGLE
                </div>
                <p style="font-size: 12px;font-weight: 500;">A diary app for Mars G. Sumagang</p>
         </div>
    </div>
</template>

<script>
import {ref} from 'vue'
import {getAuth, 
    createUserWithEmailAndPassword,
    GoogleAuthProvider, signInWithPopup
} from 'firebase/auth'
import { useRouter } from 'vue-router'
// import { userInformationStore } from '@/store/user.js'
export default {
    setup () {
        const email =ref('')
        const password = ref('')
        const router = useRouter()
        const buttonName = ref('REGISTER')
        const errorMsg = ref('')
        const errorStat = ref(false)
        const buttonStat = ref(false)   

        // const store = userInformationStore()

        const register = (e)=>{
            e.preventDefault();
            errorMsg.value=''
            errorStat.value=false
            createUserWithEmailAndPassword(getAuth(),email.value, password.value)
            .then(()=>{
                localStorage.setItem("userId",getAuth().currentUser.uid)
                router.push('/')
            }).catch((error)=>{
                console.log(error.message)
                errorStat.value=true
                switch(error.message){
                    case 'Firebase: Error (auth/email-already-in-use).':
                        errorMsg.value='Email already exist'
                        break;
                    case 'Firebase: Error (auth/invalid-email).':
                        errorMsg.value = 'Invalid Email'
                        break;
                    case 'Firebase: Password should be at least 6 characters (auth/weak-password).':
                        errorMsg.value = 'Password should be at least 6 characters'
                        break;
                    case 'Firebase: Error (auth/wrong-password).':
                        errorMsg.value='Invalid Credentials/Password'
                        break;
                    case 'Firebase: Error (auth/invalid-login-credentials).':
                        errorMsg.value='Invalid Credentials/Password'
                        break;
                    default:
                        errorMsg.value="Something went wrong! Please contact the admin"
                }
            })

        }
        const signInwithGoogle = ()=>{
            const provider = new GoogleAuthProvider();
            signInWithPopup(getAuth(),provider)
            .then((result)=>{
                localStorage.setItem("userId",getAuth().currentUser.uid)
                router.push('/')
            }).catch((error)=>{
                console.log(error)
            })
        }
        const goBackToLogIn = ()=>{
            router.push('/login')
        }

        return {email,password,register,signInwithGoogle,buttonName,goBackToLogIn,errorMsg,errorStat,buttonStat}
    }
}
</script>

<style scoped>
.content{
    border:1px solid #D8BFD8;
    max-width: 25vw;
    min-width: 300px;
    margin:0 auto;
    padding:15px;
    background-color: #fff;
    border-radius: 5px;
    margin-top: 10vh;
    text-align: center;
    box-shadow: 0 3px 5px 0 rgba(0, 0, 0, 0.3);
}
.title{
    color:var(--main-color);
    margin: 5px 0px 10px 0px;
    font-size: 25px;
    font-weight: 500;
    text-shadow: rgb(102, 102, 102) 0.5px 0.5px 0.5px, rgb(102, 102, 102) 1px 1px 1px;
}
.sub-title{
    color:var(--main-color);
    margin: 5px 0px 20px 0px;
    font-size: 15px;
}
input{
    width: 80%;
    font-size: 16px;
}
.page{
    background-color: #D8BFD8;
    height: 100vh;
    width: 100vw;
    position: fixed;
}
.form-fields{
    margin-bottom: 20px;
}
/* .footer{
    border-top: 1px solid;
    padding-top: 10px;
} */
button{
    width:85%;
    font-size: 1.3rem;
}
p{
    font-size: 16px;
}
.disabled-btn{
    cursor: not-allowed;
    background-color: var(--main-color);
    border:1px solid;
    cursor: pointer;
    color:#fff;
    padding:10px;
    border-radius: 5px;
    cursor: not-allowed;
}
.google{
    border:1px solid;
    padding:10px;
    width:80%;
    margin: 0 auto;
    border-radius: 5px;
    cursor: pointer;
    color:var(--main-color);
    font-weight: bold;
    
}
.google:hover{
    border:1px solid var(--main-color);
    background-color: #D8BFD8;
}
.click-me{
    font-weight: bold;
    cursor: pointer;
}
.click-me:hover{
   text-decoration: underline;
}
.go-back{
    font-weight: bold;
    cursor: pointer;
}
.go-back:hover{
    text-decoration: underline;
}
</style>